






// requires and returns all modules that match
const requireAll = (requireContext: __WebpackModuleApi.RequireContext) => requireContext.keys().map(requireContext);

// import all svg
try {
  requireAll(require.context('../assets/icons', true, /\.svg$/));
} catch (error) {
  console.log(error);
}
export default {
  name: 'Icon',
  props: ['name']
};
