


















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import FormItem from '@/components/FormItem.vue';

@Component({
  components: {FormItem}
})
export default class EditLabel extends Vue {
  get currentTag() {
    return this.$store.state.currentTag;
  }

  newName = 'undefined';
  h = document.body.clientHeight

  created() {
    this.$store.commit('findTag', this.$route.params.id);
  }

  goBack() {
    this.$router.go(-1);
  }
  delTag() {
    this.$store.commit('removeTag', this.currentTag.id);
    this.goBack();
  }
  updateTags(name: string) {
    this.newName = name.trim();
  }
  save() {
    if (!this.newName) return window.alert('标签名不能为空');
    if(this.newName === 'undefined'){
      this.goBack()
    } else{
      if (this.newName.length > 4) return window.alert('最多只能输入四个字');
      this.$store.commit('updateTag', {id: this.currentTag.id, name: this.newName});
      this.goBack()
    }
  }
}
