import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);


let id = parseInt(window.localStorage.getItem('ID_MAX') || '0');
function createId() {
  id++;
  window.localStorage.setItem('ID_MAX', id.toString());
  return id;
}
type RootState = {
  tags: Tag[];
  records: RecordItem[];
  currentTag?: Tag;
}
const store = new Vuex.Store({
  state: {
    tags: [],
    records:[],
    currentTag: undefined
  } as RootState,
  mutations: {
    initRecord(state){
      state.records = JSON.parse(window.localStorage.getItem('records') || '[]')
    },
    saveRecord(state,payload: RecordItem){
      state.records.push(payload)
      window.localStorage.setItem('records',JSON.stringify(state.records))
    },
    createRecord: (state, record: RecordItem) => {
      const tempRecord: RecordItem = JSON.parse(JSON.stringify(record))
      state.records.push(tempRecord)
      window.localStorage.setItem('records',JSON.stringify(state.records))
    },
    deleteRecord(state,payload){
      state.records = payload
      window.localStorage.setItem('records',JSON.stringify(state.records))
    },

    initTags(state) {
      state.tags = JSON.parse(window.localStorage.getItem('tags') || '[]');
      if(state.tags.length === 0){
        id = 0
        const initDate = ["衣","食","住","行"]
        for(let i=0;i<initDate.length;i++){
          store.commit('createTag',initDate[i])
        }
      }
    },
    createTag(state, name: string) {

      const id = createId().toString();
      state.tags.push({id, name: name});
      store.commit('saveTags');
    },
    findTag(state, id: string) {
      store.commit('initTags');
      state.currentTag = state.tags.filter(t => t.id === id)[0];
    },
    removeTag(state, id: string) {
      store.commit('initTags');
      let index = -1;
      for (let i = 0; i < state.tags.length; i++) {
        if (state.tags[i].id === id) {
          index = i;
          break;
        }
      }
      if (index !== -1) {
        state.tags.splice(index, 1);
        store.commit('saveTags');
        window.alert('删除成功');
      } else {
        window.alert('删除失败');
      }
    },
    updateTag(state, payload: { id: string; name: string }) {
      const {id, name} = payload;
      const idList = state.tags.map(item => item.id);
      if (idList.indexOf(id) >= 0) {
        const names = state.tags.map(item => item.name)
        if(names.indexOf(name) >= 0){
          window.alert('标签名重复了')
        } else {
          const tag = state.tags.filter(item => item.id === id)[0]
          tag.name = name
          store.commit('saveTags')
        }
      }
    },
    saveTags(state) {
      window.localStorage.setItem('tags', JSON.stringify(state.tags));
    }


  },
  actions: {},
  modules: {}
});
export default store;
