















































import Head from '@/components/Head.vue';
import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import dayjs from 'dayjs';
import Delete from '@/components/Delete.vue';

@Component({
  components: {Delete, Head},
  filters: {
    tagString: (tags: Tag[]) => {return tags.length === 0 ? '无' : tags.join(',');}
  }
})
export default class Statics extends Vue {
  get records() {
    return this.$store.state.records;
  }
  get groupList() {
    const {records} = this;
    const newList = JSON.parse(JSON.stringify(records)).sort((a: any, b: any) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    if (newList.length === 0) return [];
    type Result = { title: string; total1?: number; total2?: number; items: RecordItem[] }[]
    const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      const addGroup = group.items.filter(item => item.type === '+');
      const reduceGroup = group.items.filter(item => item.type === '-');
      group.total1 = addGroup.reduce(((sum, item) => sum + parseFloat(item.amount)), 0);
      group.total2 = reduceGroup.reduce(((sum, item) => sum + parseFloat(item.amount)), 0);
    });
    return result;
  }

  created() {
    this.$store.commit('initRecord');
  }

  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, 'day')) {
      return '今天';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天';
    } else if (day.isSame(now, 'year')) {
      return day.format('M月DD日');
    } else {
      return day.format('MM月DD日');
    }
  }
  deleteLine(id: any) {
    const temp = this.records.filter((item: RecordItem) => item.createdAt !== id)
    this.$store.commit('deleteRecord',temp)
  }
}
